@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100&family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100&family=Raleway:wght@100&family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@200;400&display=swap);
*{

    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Josefin Sans''Arial Narrow', Arial, sans-serif;
}

.main_div{
    width: 100%;
    height: 100vh;
    background: #6983aa;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;

}
.center_div{
    width: 25%;
    height: 80vh;
    background-color: #f4f4f4;
    box-shadow: 5px 5px 5px 25px rgba(0, 0 , 0, 0.5);
    border-radius: 10px;

}
h1{
    color: white;
    background: transparent;
    background-color: #8566aa;
    padding: 6px 0px 2px 0px;
    margin-bottom: 10px;
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}
input{
    text-align: center;
    height: 30px;
    top: 10px;
    border: none;
    background: transparent;
    font-size: 20px;
    font-weight: 500;
    width: 60%;
    border-bottom: 2px solid #8566aa;
    outline: none;
}
.center_div .newBtn{
    min-height: 40px;
    width: 40px;
    border-radius: 50%;
    border-color: none;
    background-color: #8566aa;
    color: #fff;
    border: none;
    outline: none;
    margin-left: 10px;
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
    
}
.center_div .newBtn:hover{
    background-color: green;
}
ol{
    margin-top: 30px;

}
ol li{
    padding-left: 0;
    text-align: left;
    font-size: 20px;
    font-weight: 500;
    min-height: 40px;
    display: flex;
    align-items: center;
    color: #8566aa;
    text-transform: capitalize;

}
.todo_style{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;

}
.todo_style .fa-times{
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    background-color: #8566aa;
    border-radius: 50%;
    margin: 0px 15px 0px 35px;
    color: aliceblue;
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}

.todo_style .fa-times:hover .list_style{
    background-color: crimson;

}
.todo_style .fa-times:hover{
    background-color: crimson;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
}

.navbar{

    background-color: #fff !important;
}

.navbar-brand{
    
    font-size: 1.9rem !important;
    color: #565387 !important;


}

.navbar a{
    font-size: 1.3rem;
    text-transform: capitalize;
}
.menu_active{
    font-weight: bold;
    border-bottom: 1px solid #565387;
}
.navbar a:hover {
    color: #3498db !important;
}
@media(max-width: 991px){
    .navbar-brand{
        font-size: 1.5rem !important;
    }
    .menu_active{
        font-weight: bold;
        border-bottom: none;
    }

    


}

/*main leading section */

#header{
    width:100% ;
    height: 80vh;
    margin-top: 5%;
}
#header h1{
    margin: 0px 0px 10px 0px;
    font-size: 40px;
    font-weight: 700;
    color: #000;
}
#header .brand-name{
    color: #3498db;
}
#header h2{
    color: #484848;
    font-size: 24px;
}
#header .btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.1s;
    color:#3498db;
    border: 2px solid #3498db;
    text-decoration: none;
    
}
.btn-get-started:hover{

    background:grey;
    color: #fff;

}

.header-img{
    text-align: right;
}
#header .animeted{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-dwon{

    0%{
        transform: translateY(10px);
    }
   100%{
        transform: translateY(-10px);
    }

}

@keyframes up-dwon{

    0%{
        transform: translateY(10px);
    }
   100%{
        transform: translateY(-10px);
    }

}




@-webkit-keyframes  up-down{

    0%{
        transform: translateY(10px);
    }
   100%{
        transform: translateY(-10px);
    }
}




@keyframes  up-down{

    0%{
        transform: translateY(10px);
    }
   100%{
        transform: translateY(-10px);
    }
}
/*footer */
.footer h3{
    margin: 0;
    padding: 0;
}

.searchbar{
    width: 50%;
    height: auto;
    margin: 0 auto;
    text-align: center;
}
.searchbar input{
    margin: 30px 0;
    padding: 5px 10px;
    border-radius: 5px;
    font-family: 'Courier New', Courier, monospace;
}

.searchimg img {

    border-radius: 50%;
    width:"300" ;
    height:"300";
    text-align: center;
}











/* responsive */
@media(max-width: 991px){

    #header {
        height: 80vh;
        text-align: center;

    }
    #header .header-img {
        text-align: center;
    }
    #header .header-img img{
        width: 60%;
    }
    .footer p{
        margin: 0;
        padding: 0;
    }
}
